import {
    Button,
    Container,
    Grid, 
    Image,
    Dimmer
} from 'semantic-ui-react'
import Nav from '../components/Nav'

class About extends React.Component {

    state = {
        resume: 'static/GregResume3.pdf', 
        dimmerActive: false
    }

    render(){
        return(
            <Container>
                <Dimmer page active={this.state.dimmerActive} onClick={this.handleHide}>
                        <Container>
                            <Image src={this.state.resume}  verticalAlign="top" size="massive"/>
                        </Container>
                </Dimmer>
                <Grid>
                    <Grid.Row>
                    <Grid.Column width={8}>
                        <h1>Education:</h1>
                        <h2>
                            The Flatiron School (New York)<br /> - Software Engineering '18<br/><br/>
                            University of the Arts (Philadelphia)<br/> - BFA Interactive Digital Media / Music '17<br/>
                        </h2>

                        <h2 onClick={() => this.setState({dimmerActive: !this.state.dimmerActive})}>

                        </h2>

                    </Grid.Column>
                    <Grid.Column width={8}>
                    <p>
                    
                    </p>
                    </Grid.Column>
                    </Grid.Row>
                </Grid>
                <style jsx>
                {
                    `
                        p, h1{
                            font-size: larger;
                            font-family:"Lucida Console", Monaco, monospace;
                        }
                        h2{
                            font-size: medium;
                            font-family:"Lucida Console", Monaco, monospace;

                        }
                    `
                }
                </style>
            </Container>
        )
    }
}

export default About


